	module.exports = {"data":{"post":{"html":"<p>Now more than ever, our morning coffee is a vital necessity in order to start the day. So it was with no small amount of horror that we discovered yesterday that our <a href=\"http://www.papascott.de/archives/2007/04/24/jura-xf50/\">Jura coffee machine</a> would no longer produce coffee. The beans would grind, the water would pump, but only a few drops of hot brown elixir would trickle into our cups. No error message was displayed, and the problem persisted even after changing the water filter and running the cleaning program twice.</p>\n<p>Then came Google to the rescue. Searching for \"Jura selbst reparieren\" (repair Jura yourself) brought up the site <a href=\"http://www.biomess.de/jura/index.html\">\"Selber schrauben an Jura\"</a>, where we found that a <a href=\"http://www.biomess.de/jura/html/faq.html\">common problem</a> (FAQ 15) is that the brew filter becomes clogged. It can be easily cleaned with your finger after reaching with your hand through the tray opening.</p>\n<p>Now if Google would only brew the coffee for us every morning as well, it would be almost worth all the private data they are collecting from us.</p>","fields":{"title":"Google Saved Our Morning Coffee","date":"14 Jul 2007","url":"https://papascott.de/2007-07-14-google-saved-our-morning-coffee/"}}},"pathContext":{"slug":"/2007-07-14-google-saved-our-morning-coffee/","prev":{"fields":{"title":"Ten Days In","date":"11 Jul 2007","slug":"/2007-07-11-ten-days-in/"}},"next":{"fields":{"title":"Minus Five","date":"15 Jul 2007","slug":"/2007-07-15-minus-five/"}}}}

